.hero{
    width: 100% ;
    height: 100vh;
    position: relative;
    background: linear-gradient(to bottom, #add8ed, #fff);
}
.hero-mid{
    width: 100% ;
    height: 70vh;
    position: relative;
   
}
.hero-mid .hero-text h1{
    padding-top: 4rem !important;
    text-align: left !important;
    
}

img{
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.gradient-overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom,
       rgba(27, 16, 90, 0.7), rgba(27, 16, 90, 0.7));

}
.hero-text{
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
}
.hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    background: #fff;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}
.hero-text h4{
    font-size: 2rem;
    color: #fff;
    padding-bottom: 3rem;
   
}
.hero-text p{
    font-size: 1.1rem;
    color: #fff;
    padding:  2rem;
    text-align: center;
}

.hero-text .show{
    text-decoration: none;
    background-color: white;
    padding: 1rem 2rem;
    border-radius: 2px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: #222;
}
.hero-text .hide{
    display: none;
}
@media screen and (max-width: 555px) {
    .hero-text h1{
        padding: 10px 20px;
    }
    .hero-text p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;
    }
    .hero-text .show{
        padding: .6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;
        background: #4D34DD;
    }
}